<template>
  <v-data-table
    :headers="dessertHeaders"
    :items="elementos"
    single-expand
    :expanded.sync="expanded"
    item-key="No_trabajador"
    show-expand
    class="elevation-1"
    hide-default-footer
    dark
  >
    
    <template v-slot:expanded-item="{ headers, item }">
      <td :colspan="headers.length">
         <v-container class="">
            <v-row>
              <v-col cols="12" md="2">
                    <v-btn block outlined color="red" @click="Eliminar(item)">Borrar</v-btn>                
              </v-col>              
              <v-col cols="12" md="5">
                <p class="text-h6">Nombre: {{ item.Nombre }}</p>
              </v-col>                          
              <v-col cols="12" md="5">
                <p class="text-h6">Numero de trabajador: {{ item.No_trabajador }}</p>
              </v-col>              
              <v-col cols="12" md="6">
                <p class="text-h6">Horas Semanales: {{ item.h_por_semana}}</p>
              </v-col>              
            </v-row>
            <v-row>
              <Horario_S :datos="item.horario" />
            </v-row>
          </v-container>
        
      </td>
    </template>
  </v-data-table>
</template>
<script>
import Horario_S from '../components/Horario_semanal.vue'
import axios from "axios";
import { mapMutations, mapState } from "vuex";
  export default {
    computed: {
    ...mapState(["ip"]),
  },
    props: {    
    elementos: {
      type: Array,
      default: function () {
        return []
      }
    },},
    data () {
      return {
        expanded: [],
        singleExpand: false,
        dessertHeaders: [
          {
            text: 'Nombre',
            align: 'start',
            sortable: false,
            value: 'Nombre',
          },
          { text: 'Horas Por Semana', value: 'h_por_semana' },          
          { text: '', value: 'data-table-expand' },
        ],
        
      }
    },
    methods: {
      ...mapMutations([
      "Modificar_cargar",
      "Checar_Token",
      "Salir",
      "Modificar_Informacion",
      "Modificar_Espera",
      "Modificar_Correcto",
      "Modificar_Datos",
      "Modificar_Boton",
      "Modificar_ruta",
    ]),
      async Eliminar(item) {
      this.Modificar_cargar(true);
      this.Modificar_Informacion("Eliminando Docente");
      this.Modificar_Espera(true);

      const json = JSON.stringify({
        token: localStorage.getItem("Token"),
        id: item.No_trabajador,
      });
      
      try {
        
        let res = await axios.post(
          "http://" + this.ip + "/api/jefe_departamento/desvincular-docente",
          json,
          {
            headers: {
              // Overwrite Axios's automatically set Content-Type
              "Content-Type": "application/json",
            },
          }
        );
        
        if (!this.error && !res.data.Error) {
          this.Modificar_Espera(false);
          this.Modificar_Correcto(true);
          this.Modificar_Informacion("Se Elimino");
          
          this.Modificar_Boton(true);          
          this.$router.go(0);
          
        } else if (res.data.Error) {
          
          this.Modificar_Espera(false);
          this.Modificar_Correcto(false);
          this.Modificar_Datos(res.data.status);
          this.Modificar_Boton(true);
          
        }
      } catch (error) {
        this.Modificar_Espera(false);
        this.Modificar_Correcto(false);
        this.Modificar_Boton(true);
        
        console.log(error);
      }
    },
    },
    components: {
      Horario_S,
    },
  }
</script>