<template>
  <v-sheet color="rgba(0,0,0,0.6)">
    <v-container class="white--text">
      <v-row justify="center" class="text-center">
        <v-col cols="12">
          <span class="text-md-h3 text-h5">Administrar Docentes</span>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12" md="11"> <Tabla_Datos_Docentes :elementos="Docentes" /> </v-col>
        <v-col cols="12" md="1" class="text-center">
          <v-dialog v-model="dialog" persistent max-width="600px">
            <template v-slot:activator="{ on, attrs }">
              <v-btn color="green" dark v-bind="attrs" v-on="on" outlined block>
                Agregar
              </v-btn>
            </template>
            <v-card color="rgba(0,0,0,0.9)" class="white--text">
              <v-card-title>
                <span class="text-h5 text-center">Agregar Docente</span>
              </v-card-title>
              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col cols="12">
                      <v-col cols="12">
                        <span class="white--text">No De Trabajador</span>
                        <v-otp-input
                          dark
                          ref="otpInput"
                          length="7"
                          plain
                          value=""
                          type="number"
                          v-model="nuevo.no_trabajador"
                          @finish="Cargar()"
                        ></v-otp-input>
                      </v-col>
                    </v-col>
                    <v-col cols="12" md="6" v-if="existe">
                      <v-text-field
                        label="Nombre"
                        :loading="loading"
                        disabled
                        dark
                        v-model="nuevo.Nombre"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" md="6" v-if="existe">
                      <v-text-field
                        label="Apellido Paterno"
                        :loading="loading"
                        dark
                        disabled
                        v-model="nuevo.ApellidoP"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" md="6" v-if="existe">
                      <v-text-field
                        label="Apellido Materno"
                        :loading="loading"
                        dark
                        disabled
                        v-model="nuevo.ApellidoM"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" md="6" v-if="existe">
                      <v-text-field
                        label="Horas A La Semana"
                        :loading="loading"
                        dark
                        disabled
                        v-model="nuevo.Horas_Semanales"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="red darken-1" text @click="Cancelar()">
                  Cancelar
                </v-btn>
                <v-btn
                  color="green darken-1"
                  text
                  @click="Agregar_Docente()"
                  v-if="existe"
                >
                  Agregar
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-col>
      </v-row>
    </v-container>
  </v-sheet>
</template>
<script>
import Tabla_Datos_Docentes from "../components/Tabla_Datos_Docentes.vue";
import axios from "axios";
import { mapMutations, mapState } from "vuex";
export default {
  mounted() {
    this.Cargar_Datos();
  },
  data() {
    return {
      dialog: false,
      existe: false,
      loading: true,
      nuevo: {
        no_trabajador: "",
        Nombre: "",
        ApellidoP: "",
        ApellidoM: "",
        Horas_Semanales: "",
      },
      Docentes: [],
      horario: [],
      id: null,
      Horas_T: null,
    };
  },
  computed: {
    ...mapState(["ip"]),
  },
  methods: {
    ...mapMutations([
      "Modificar_cargar",
      "Checar_Token",
      "Salir",
      "Modificar_Informacion",
      "Modificar_Espera",
      "Modificar_Correcto",
      "Modificar_Datos",
      "Modificar_Boton",
      "Modificar_ruta",
    ]),

    async Buscar_Docente() {
      this.Modificar_cargar(true);
      this.Modificar_Informacion("Verificando Docente");
      this.Modificar_Espera(true);

      const json = JSON.stringify({
        token: localStorage.getItem("Token"),
        id: this.nuevo.no_trabajador,
      });
      
      try {
        // let res = await axios.post(
        //   "http://" + this.ip + "/api/jefe_departamento/vincular-docente",
        //   json,
        //   {
        //     headers: {
        //       // Overwrite Axios's automatically set Content-Type
        //       "Content-Type": "application/json",
        //     },
        //   }
        // );
        let res = await axios.post(
          "http://" + this.ip + "/api/jefe_departamento/check-vinculo",
          json,
          {
            headers: {
              // Overwrite Axios's automatically set Content-Type
              "Content-Type": "application/json",
            },
          }
        );
        
        if (!this.error && !res.data.Error) {
          this.Modificar_Espera(false);
          this.Modificar_Correcto(true);
          this.Modificar_Informacion("Correcta");
          this.nuevo.Nombre = res.data.Datos[0].Nombre;
          this.nuevo.ApellidoP = res.data.Datos[0].ApellidoP;
          this.nuevo.ApellidoM = res.data.Datos[0].ApellidoM;
          let Horas = 0;
          let min = 0;

          if (res.data.Horario) {
            res.data.Horario.forEach((element) => {
              const inicio =
                parseInt(element.Entrada.substring(0, 2), 10) * 60 +
                parseInt(element.Entrada.substring(3, 5), 10);
              const fin =
                parseInt(element.Salida.substring(0, 2), 10) * 60 +
                parseInt(element.Salida.substring(3, 5), 10);
              const minutos = fin - inicio;
              min += minutos;
            });
          }
          Horas = Math.floor(min / 60);
          min = min - Horas * 60;
          const HorasT =
            (Horas < 10 ? "0" + Horas : Horas + "") +
            ":" +
            (min < 10 ? "0" + min : min + "") +
            ":00";

          this.nuevo.Horas_Semanales = HorasT;

          this.Modificar_Boton(true);
          this.loading = false;
          this.existe = true;
        } else if (res.data.Error) {
          
          this.Modificar_Espera(false);
          this.Modificar_Correcto(false);
          this.Modificar_Datos(res.data.status);
          this.Modificar_Boton(true);
          this.loading = false;
          this.existe = false;
        }
      } catch (error) {
        this.Modificar_Espera(false);
        this.Modificar_Correcto(false);
        this.Modificar_Boton(true);
        this.existe = false;
        console.log(error);
      }
    },
    async Agregar_Docente() {
      this.Modificar_cargar(true);
      this.Modificar_Informacion("Agregando Docente");
      this.Modificar_Espera(true);

      const json = JSON.stringify({
        token: localStorage.getItem("Token"),
        id: this.nuevo.no_trabajador,
      });
      
      try {
        let res = await axios.post(
          "http://" + this.ip + "/api/jefe_departamento/vincular-docente",
          json,
          {
            headers: {
              // Overwrite Axios's automatically set Content-Type
              "Content-Type": "application/json",
            },
          }
        );

        
        if (!this.error && !res.data.Error) {
          this.Modificar_Espera(false);
          this.Modificar_Correcto(true);
          this.Modificar_Informacion("Se Agrego Docente");

          this.Modificar_Boton(true);

          this.Cancelar();
          this.Cargar_Datos();
        } else if (res.data.Error) {
          
          this.Modificar_Espera(false);
          this.Modificar_Correcto(false);
          this.Modificar_Datos(res.data.status);
          this.Modificar_Boton(true);
          this.loading = false;
          this.existe = false;
        }
      } catch (error) {
        this.Modificar_Espera(false);
        this.Modificar_Correcto(false);
        this.Modificar_Boton(true);
        this.existe = false;
        console.log(error);
      }
    },
    async Cargar_Datos() {
      this.Modificar_cargar(true);
      this.Modificar_Informacion("Verificando Docente");
      this.Modificar_Espera(true);

      const json = JSON.stringify({
        token: localStorage.getItem("Token"),
      });

      try {
        let res = await axios.post(
          "http://" + this.ip + "/api/jefe_departamento/docentes",
          json,
          {
            headers: {
              // Overwrite Axios's automatically set Content-Type
              "Content-Type": "application/json",
            },
          }
        );
        if (!this.error && !res.data.Error) {
          this.Modificar_Espera(false);
          this.Modificar_Correcto(true);
          this.Modificar_Informacion("Correcta");
          
          
          res.data.Datos.forEach((element) => {            
            const datos= {Nombre: element.Nombre,No_trabajador:element.No_Empleado }
            this.Valores(element, datos);
          });
          this.Modificar_Boton(true);
          this.loading = false;
          
        } else if (res.data.Error) {
          
          this.Modificar_Espera(false);
          this.Modificar_Correcto(false);
          this.Modificar_Datos("No hay registros");
          this.Modificar_Boton(true);
          this.loading = false;
        }
      } catch (error) {
        this.Modificar_Espera(false);
        this.Modificar_Correcto(false);
        this.Modificar_Boton(true);
        console.log(error);
      }
    },
    async Valores(element, datos) {
      this.id = element.No_Empleado;      
      await this.Ver_Horarios();
      
       if((this.Docentes.find(dato => dato.No_trabajador ==  datos.No_trabajador))?false:true){
         
         this.horario.sort((a, b) =>   a.dia-b.dia)
         
         this.Docentes.push({
        Nombre: datos.Nombre,
        No_trabajador: datos.No_trabajador,
        h_por_semana: this.Horas_T,
        horario:this.horario
      })
       }
      
      this.horario = [];
      this.Horas_T = null;
    },
    async Ver_Horarios() {
      this.Modificar_cargar(true);
      this.Modificar_Informacion("Cargando Horario");
      this.Modificar_Espera(true);

      const json = JSON.stringify({
        token: localStorage.getItem("Token"),
        id: this.id,
      });

      try {
        let res = await axios.post(
          "http://" + this.ip + "/api/docente/horario",
          json,
          {
            headers: {
              // Overwrite Axios's automatically set Content-Type
              "Content-Type": "application/json",
            },
          }
        );

        if (!this.error && !res.data.Error) {
          this.Modificar_Espera(false);
          this.Modificar_Correcto(true);
          this.Modificar_Informacion("Correcta");
          
          let Horas = 0;
          let min = 0;

          if (res.data.Datos) {
            res.data.Datos.forEach((element) => {
              const inicio =
                parseInt(element.Entrada.substring(0, 2), 10) * 60 +
                parseInt(element.Entrada.substring(3, 5), 10);
              const fin =
                parseInt(element.Salida.substring(0, 2), 10) * 60 +
                parseInt(element.Salida.substring(3, 5), 10);
              const minutos = fin - inicio;
              min += minutos;
              this.horario.push({
                dia: element.Dia,
                entrada: element.Entrada,
                salida: element.Salida,
              });
            });
          }
          Horas = Math.floor(min / 60);
          min = min - Horas * 60;
          const HorasT =
            (Horas < 10 ? "0" + Horas : Horas + "") +
            ":" +
            (min < 10 ? "0" + min : min + "") +
            ":00";
          this.Modificar_cargar(false);
          this.loading = false;

          this.Horas_T = HorasT;
          
        } else if (res.data.Error) {
          
          this.Modificar_Espera(false);
          this.Modificar_Correcto(false);
          this.Modificar_Datos("No hay registros");
          this.Modificar_cargar(false);
          this.Modificar_Boton(true);
          this.loading = false;
        }
      } catch (error) {
        this.Modificar_Espera(false);
        this.Modificar_Correcto(false);
        this.Modificar_Boton(true);
        this.Modificar_cargar(false);
        console.log(error);
      }
    },

    Cargar() {
      this.Buscar_Docente();
    },
    Cancelar() {
      this.dialog = false;
      this.existe = false;
      if (this.nuevo.no_trabajador) {
        this.nuevo.no_trabajador = "";
      }
      if (this.nuevo.Nombre) {
        this.nuevo.Nombre = "";
      }
      if (this.nuevo.ApellidoP) {
        this.nuevo.ApellidoP = "";
      }
      if (this.nuevo.ApellidoM) {
        this.nuevo.ApellidoM = "";
      }
      if (this.nuevo.Horas_Semanales) {
        this.nuevo.Horas_Semanales = "";
      }
    },
  },
  components: {
    Tabla_Datos_Docentes,
  },
};
</script>