<template>
  <v-container>
    <v-sheet color="rgba(0,0,0,0.5)" rounded>
      <v-row class="text-center pa-0 ma-0 white--text" no-gutters>
        <v-col cols="12">
          <h1>Horario</h1>
        </v-col>
      </v-row>
      <v-row no-gutters justify="space-around">
        <v-col
          class="text-center pa-0"
          cols="12"
          md="2"
          v-for="x in ejemplo(datos)"
          :key="x.id"
        >
          <v-row no-gutters>
            <v-container class="">
              <v-row class="" v-if="x.dia" no-gutters>
                <v-col cols="12" class="pa-0 ma-0">
                  <v-sheet color="rgba(79, 134, 247)" rounded="pill">
                    <span class="text-h5 white--text">{{ x.dia }}</span>
                  </v-sheet>
                </v-col>
              </v-row>
              <v-row class="pa-0" v-if="x.entrada" no-gutters justify="center">
                
                  <v-col class="pa-0 white--text" cols="12">
                    <span class="text-button"
                      >Entrada: {{ x.entrada }} hrs.</span
                    >
                  </v-col>
                  <v-col class="pa-0 white--text" cols="12">
                    <span class="text-button"
                      >Salida: {{ x.salida }} hrs.</span
                    >
                  </v-col>
                  <v-col class="pa-0 " cols="12" v-if="x.total">
                    <span class="text-button white--text"
                      >Horas Totales: {{ x.total }} hrs.</span
                    >
                  </v-col>
                
              </v-row>
              <v-row v-if="x.total_s" no-gutters>
                <v-col cols="12" class="pa-0 " >
                  <v-sheet color="rgba(79, 134, 247)" rounded="pill">
                    <span class="text-h5 text-md-h6 white--text">Hrs Semanales</span>
                  </v-sheet>
                  
                </v-col>
              </v-row>

              <v-row
                class="pa-0"
                v-if="x.total_s"
                align="center"
                style="height: 150px"
              >
                <v-col
                  cols="12"
                  class="pa-0 ma-0 text-center"
                  align-self="center"
                >
                  <span class="text-h5 white--text">{{ x.total_s }} HRS</span>
                </v-col>
              </v-row>
            </v-container>
          </v-row>
        </v-col>
      </v-row>
    </v-sheet>
  </v-container>
</template>
<script>
export default {
  props: {
    datos: {
     type: Array, 
      default:function () {
        return []
      }
    },
  },
  data() {
    return {
      Horas_T: undefined,
    };
  },
  methods: {
    ejemplo(datos) {
      if (datos) {
        let Semana = ["Lunes", "Martes", "Miercoles", "Jueves", "Viernes"];
        const horario = [];
        let h_t = 0;
        datos.forEach((element) => {
            
          let sal =
            parseInt(element.salida.substring(0, 2)) * 60 +
            parseInt(element.salida.substring(3, 5));
            
          let entra =
            parseInt(element.entrada.substring(0, 2)) * 60 +
            parseInt(element.entrada.substring(3, 5));
            
          let horas = Math.round((sal - entra) / 60);

          let minutos = sal - entra - horas * 60;

          if (minutos < 0) {
            horas--;
            minutos = sal - entra - horas * 60;
          }
          h_t += horas * 60 + minutos;
          horario.push({
            dia: Semana[element.dia - 1],
            entrada: element.entrada,
            salida: element.salida,
            total:
              (horas < 10 ? "0" + horas : horas + "") +
              ":" +
              (minutos < 10 ? "0" + minutos : minutos + ""),
            nombre: element.nombre,
          });
        });
        let horas = Math.round(h_t / 60);

        let minutos = h_t - horas * 60;

        if (minutos < 0) {
          horas--;
          minutos = h_t - horas * 60;
        }
        horario.push({
          total_s:
            (horas < 10 ? "0" + horas : horas + "") +
            ":" +
            (minutos < 10 ? "0" + minutos : minutos + ""),
        });
        
        this.Horas_T = h_t;
        return horario;
      } else {
        return [];
      }
    },
  },
};
</script>
